const app = getApp();
export default {
	data() {
		return {
			search_word: '',
			dialogCont: true,
			default_avatar: '../../static/default-avatar.png',
			alumni_avatar: '../../static/alumni_avatar.jpg',
			alumni_sex: 2,
			male_icon: '../../static/male.png',
			female_icon: '../../static/female.png',
			is_collect: 1,
			hoverClass: 'hover2',
			itemStyle: {
				color: '#595E75',
				fontSize: '28rpx',
				marginBottom: '18rpx'
			},
			headStyle: {
				color: '#595E75'
			},
			alumniList: [],
			collected: true,
			collect_none: false
		}
	},
	onLoad() {
		if(app.reToken()) {
			uni.getStorageSync("access_token")
		}
		this.getAlumniList();
	},
	methods: {
		getAlumniList() {
			uni.showLoading()
			uni.request({
				url: getApp().globalData.url + 'api/user/collect',
				method: 'get',
				header: {
					Authorization: 'Bearer ' + uni.getStorageSync('access_token')
				},
				success: (res) => {
					uni.hideLoading()
					// console.log(res)
					this.alumniList = res.data.items
					// console.log(this.alumniList)
					if(this.alumniList.length === 0) {
						this.collect_none = true;
					} 
				},
				fail: (res) => {
					
				}
			})
		},
		addCol(id, i) {
			var _this = this;
			var requestCode = uni.request({
				url: getApp().globalData.url + 'api/user/collect',
				method: 'POST',
				data: {
					info_id: id
				},
				header: {
					Authorization: 'Bearer ' + uni.getStorageSync('access_token')
				},
				success: (res) => {
					if (res.data.errorCode == 0) {
						uni.showToast({
							icon: 'none',
							title: '取消收藏成功',
						});
						this.getAlumniList()
						uni.setStorageSync("coll_reload", true);
					}
				},
				fail: (res) => {
					console.log(res);
					uni.showToast({
						icon: 'none',
						title: '请求失败，请稍后再试',
					});
				},
			})
		},
		toSearch() {
			uni.navigateTo({
				url: './search'
			})
		},
		close_dialog() {
			this.dialogCont = false;
		},
		toBack() {
			uni.switchTab({
				url: './index'
			})
		},
	}
}
